/*공통 레이아웃 속성*/





.content {
    padding: 2rem;
    height: 90%;    /*100퍼로 하면 아래 내려감. 확인예쩡*/
    background-color: #F0EFF5;
}

.content > .content-title {
    font-size: 2rem;
    font-weight: bold;
    size: 2rem;
    margin-bottom: 0.5rem;
}
.icon {
    margin-right: 0.5rem;
}
.round-button {
    border-radius: 25px;
    border-style: none;
    padding: 0.3rem 3rem;
    font-size: 1rem;
}
.center-align {
    text-align: center;
}
.left-align {
    text-align: left;
}
.right-align {
    text-align: right;
}
.grid-container {
    display: grid;
    height: 80%;            /*100퍼로 하면 아래 내려감. 확인예쩡*/
    /* border: 1px solid rgb(204, 24, 93); */
    box-sizing: inherit;    /*부모 기준으로 크기 정함*/
    gap: 1rem;
}

.grid-item {
    background-color: rgb(255, 255, 255);
    border-radius: 1rem;
    padding: 1rem;
}

.grid-item > h4 {
    color: rgb(60, 15, 143);
    margin-bottom: 0.5rem;
}
/* ---- modal content ---- */

#modal > .overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .7);
    z-index: 1000;
}

#modal > .modal-box {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #FFF;
    padding: 2rem;
    width : 20%;
    height : 15%;
    z-index: 1000;
}
#modal .modal-content{
    height: 40%;
}
#modal button{
    width: 3rem;
    height: 1.5rem;
    margin-right: 1rem;
    background-color: #e4e4f8;
}
/* ---- destination content ---- */
#destination > .grid-container {
    grid-template-columns: 3fr 1fr;
    grid-template-rows: 1fr 4fr;
}
#destination #item-destination {
    grid-column: 1/2;
    grid-row: 1/3;
    
}
/* ---- map content ---- */
#map .grid-container {
    grid-template-columns: 4fr 1fr;
    grid-template-rows: 1fr 1fr;
}

#map #item-map {
    grid-column: 1;
    grid-row: 1/3;
    
}

.target {

    position: absolute;
    z-index: 9;
    /* background-color: rgb(0, 0, 0); */

    height:3px;
    background-color: rgba(255, 238, 0, 0.774);
    
}
.robot {
    /* width: 1.5rem;
    height: 1.5rem; */
    border-radius: 50%;
    position: absolute;
    z-index: 10;
    background-color: rgb(0, 0, 0);
}
.num-robot-img {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 11;
}
#map-container{
    position: relative;
    border: 1px solid rgb(102, 15, 143);
    /* width: 40rem; */
    /* height: 30rem; */
    height: 90%;
}
.map-tile {
        /* width: 1rem;
    height: 1rem; */
    border: 1px solid rgb(38, 15, 143);
    position: absolute;
    z-index: 2;
}

.map-tile-node-id{
    position: absolute;
    z-index: 2;
}
.map-tile-img {
    position: absolute;
    z-index: 1;
}

#map .pickup-button {
    padding: 0.3rem;
    margin-bottom: 0.5rem;
}
#map .pickup-button:hover {
    background-color: rgba(218, 219, 252, 0.993);
}

#map .map-background-img {
    width: 100%;
    height: 100%;
}
/* ---- pickup content ---- */
.pickup-item {
    overflow: hidden; /*부모 높이를 잊어벼리기때문에 다시 높이를 생기게끔해주는 float해재 기법*/
    padding: 1rem 2rem;
}

.pickup-item > *{
    float: left; /*가로 정렬을 하기위한 float*/
    /* padding: 0rem 1rem; */
}
.pickup-item > .pickup-number{
    width: 2rem;
    font-size: 2rem;
}
.pickup-store {
    width: 5rem;
}
/* ---- user content ---- */
#user > .grid-container {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 2fr;
}

#user #item-profile {
    grid-column: 1/4;
    grid-row: 1;
}

#user #item-profile > img{
    width: 3rem;
}
/* ---- call content ---- */
#call > .grid-container {
    grid-template-columns: 2fr 1fr;
    /* grid-template-rows: 1fr 3fr; */
}

#call #item-button {
    grid-column: 1/2;
    /* grid-row: 1/3; */
}

#call #item-button  #call-button {
    background-color: rgb(209, 215, 241);
    font-size: 3rem;
    width: 20rem;
    height: 10rem;
    border: 1px solid rgb(102, 15, 143);
    
    /* padding: 1rem 3rem; */
}
#call > .grid-container  .grid-container {  
    /* .call-status */
    grid-template-rows: 1fr 3fr;
    height: 100%;

}
.data-reload {
    border: 1px solid rgb(102, 15, 143);
    background-color: aliceblue;
    padding: 1rem;
}

/* ---- login content ---- */

#login > .grid-container{
    height: 100%;
    grid-template-columns: 1fr 5fr 1fr;
    /* border: 1px solid rgb(235, 179, 75); */
}
/* 그리드 내부 아이템 속성 */
#login #item-login{
    grid-column: 2/3;
    display: block;
    padding: 3rem 3rem;
    height: max-content;
}
#login #item-login > *{
    margin: 1.2rem;
}
#login .login-hprobot-img {
    width: 50%;
}

.login-input-div {      /* 로그인 아이콘+입력칸 각각의 쌍을 다룸 */
    width: 70%;
    display: inline-block;
    margin-bottom: 1.5rem;
}
#login-div {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
}

/* ---- authorized content ---- */

#authorized > .grid-container{
    height: 100%;
    grid-template-columns: 1fr 5fr 1fr;
    /* border: 1px solid rgb(235, 179, 75); */
}
#authorized #item-authorized {
    grid-column: 2/3;
    display: block;
    height: max-content;
    font-size: 2rem;
    padding: 5rem 0rem;
}
#item-authorized *{
    margin: 1rem;
}
#warning-icon{
    font-size: 6rem;
}
